import React from 'react'
import { Swiper, SwiperSlide } from 'swiper/react'
import 'swiper/swiper-bundle.min.css';
import 'swiper/swiper.min.css';
import 'swiper/components/navigation/navigation.min.css';
import SwiperCore, { Navigation, EffectFade } from 'swiper';
import { faCaretRight, faCaretLeft } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import './synopsys.css'
function Synopsys() {
    SwiperCore.use ([Navigation, EffectFade]);

    const swiperStyle = {
        position: 'relative',
        width: '100%',
        height: '100%',
    }
    const swiperNavigation = {
        nextEl: '.swiper-next',
        prevEl: '.swiper-prev'
    }

    const charWrite = episode.map(ep =>{
        return(
            <SwiperSlide key={ep.id}>
                <div className="synopsis_bg" style={ep.style}>
                    <div className="synopsis_info">
                        <h2>EP {ep.id} : {ep.title}</h2>
                        <p>{ep.synopsis}</p>
                    </div>
                </div>
            </SwiperSlide>
        )
    });

    return (
        <section className='synopsis'>
            <Swiper
                effect={'fade'}
                speed={1000}
                style={swiperStyle}
                navigation={swiperNavigation}
                loop={true}
            >
                {charWrite}
                <button type='button' className='swiper-prev'>
                    <FontAwesomeIcon icon={faCaretLeft} />
                    <span>Prev</span>
                </button>
                <button type='button' className='swiper-next'>
                    <FontAwesomeIcon icon={faCaretRight} />
                    <span>Next</span>
                </button>
            </Swiper>
        </section>
    )
}
export default Synopsys
