import React,{useEffect} from 'react'
import { Link } from 'react-router-dom'
import './header.css'
function Header() {
    const gnbFolder = {background : 'url("./svg/folder.svg") no-repeat',backgroundSize:'cover' } 
    const gnbFile = {background : 'url("./svg/folder__file.svg") no-repeat',backgroundSize:'cover' } 
    /* useEffect Hook */
    useEffect(()=>{
        const diskTime = document.querySelector('.disk_time');
        const nowHour = document.querySelector('.time_nowHour');
        const nowMinute = document.querySelector('.time_nowMinute');
        //  
        let today;
        let hh;
        let mm;

        /*   */
        let recordTime = 98;
        setInterval(() => {
            if(recordTime>1){
                diskTime.innerHTML = recordTime;
                recordTime = recordTime-1;
            }else{
                diskTime.innerHTML = '99'
                recordTime = 98;
            }
        }, 1000*60);

        /*    */
        setInterval(() => {
            today = new Date();
            hh = (today.getHours()<10? '0'+today.getHours():today.getHours()); // 1~9  0 
            mm = (today.getMinutes()<10? '0'+today.getMinutes():today.getMinutes()); // 1~9  0
            nowHour.innerHTML = hh;
            nowMinute.innerHTML = (hh<12?`${mm} AM`:`${mm} PM`);
        }, 1000);
    },[]);

  return (
    <>
        <aside>
            {/*   */}
            <div className='camera_scroll'>
                <span>W</span>
                <div className='scroll_box'>
                    <span className='scroll_bar'></span>
                </div>
                <span>T</span>
            </div>
            {/*   = ,   */}
            <div className='camera_info'>
                <div className='info_icon'>
                    <img src="./svg/battery.svg" alt="battery" className='svg info_battery' /> 
                    <img src="./svg/play.svg" alt="recoding" className='svg info_play' /> 
                </div>
                {/*   */}
                <div className="info_recordTime">
                    <span className="random_time"></span>
                    :
                    <span className="random_time"></span>
                    :
                    <span className="random_time"></span>
                </div>
                {/*   */}
                <div className="info_capacity">
                    <img src="./svg/record.svg" alt="disk capacity" className='svg info_disk'/>
                    <span className="disk_text"><span className="disk_time">99</span>min</span>
                </div>
            </div>
            {/*    */}
            <div className="camera_time">
                <span className="time_nowHour">00</span>
                <span className="colon_blink">:</span>
                <span className="time_nowMinute">00 AM</span>
            </div>
        </aside>

        <header>
            {/*   gnb  */}
            <input type="checkbox" id='gnbBtn' />
            <ul className="gnb">
                <li><Link to="/">Home</Link></li>
                <li><Link to="/trailer">Trailer</Link></li>
                <li><Link to="/characters">Characters</Link></li>
                <li><Link to="/synopsys">Synopsis</Link></li>
                {/* rel='noreferrer'        */}
                <li><a href="https://www.disneyplus.com/ko-kr/home" target='_blank' rel='noreferrer'>Disney+</a></li>
            </ul>
            {/*   */}
            <button type='button'>
                <label htmlFor="gnbBtn">
                    <div className="folder" style={gnbFolder}>
                        <div className="file" style={gnbFile}>
                            
                        </div>
                    </div>
                </label>
            </button>
        </header>
    </>
  )
}

export default Header